.game-filter {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 16px;
    border-bottom: 1px solid #ebeef1;
}

.queue-filters {
    display: flex;
    gap: 12px;
}

.filter-btn {
    padding: 8px 16px;
    border: none;
    background: none;
    color: #758592;
    font-size: 11px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
    border-radius: 4px;
}

.filter-btn:hover {
    background: #f7f7f9;
    color: #202d37;
}

.filter-btn.active {
    background: #5383e8;
    color: white;
}

.champion-search {
    position: relative;
}

.champion-input {
    padding: 8px 16px;
    border: 1px solid #ebeef1;
    border-radius: 4px;
    font-size: 14px;
    width: 200px;
    outline: none;
    transition: border-color 0.2s ease;
}

.champion-input:focus {
    border-color: #5383e8;
}

.champion-input::placeholder {
    color: #758592;
} 