.navigation-tabs {
    background: #fff;
    padding: 0 32px;
    margin: 0 auto;
    max-width: 1080px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ebeef1;
}

.tab-buttons {
    display: flex;
    gap: 32px;
}

.tab {
    padding: 16px 8px;
    border: none;
    background: none;
    font-size: 16px;
    font-weight: 600;
    color: #758592;
    cursor: pointer;
    position: relative;
}

.tab.active {
    color: #202d37;
}

.tab.active::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: #5383e8;
}


.icon {
    width: 16px;
    height: 16px;
    border: 1px solid #758592;
    border-radius: 50%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
} 